<template>
  <!-- 房间日历 -->
  <div class="topbox">
    <p
      class="scale"
      @click="pickScaleName"
    >{{ scaleName }}</p>
    <div>
      <div
        v-for="(n, nindex) in showArray"
        :key="nindex"
      >
        <h4 class="time">{{ n.title.y + "年" + n.title.m + "月" }}</h4>
        <div>
          <div
            v-for="item in weekList"
            class="datediv"
            style="line-height:50px;"
            :key="item"
          >{{ item }}</div>
          <div
            v-for="(item, index) in n.list"
            class="datediv"
            :style="item.holiday?'background-color:#FFF0F0;box-sizing:border-box;':'border:none'"
            :key="index"
            @click="changePrice(n.title, item, nindex, index)"
            :id="'choose'+index"
          >
            <div v-if="item.price">
              <div style="color:#000;font-size:12px;">{{ item.date }}</div>
              <div
                v-if="item.price >= 0"
                class="money"
                style="color:#f60;"
              >￥{{ Math.trunc(item.price) }}</div>
              <div style="font-size:12px;">{{ item.holiday }}</div>
              <div v-if="$route.query.sign && item.date">{{ item.maxSaleCount - item.virtualRoomBook < item.roomCount - item.roomBook ? item.maxSaleCount - item.virtualRoomBook : item.maxSaleCount - item.roomBook }}间</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 一天 -->
    <van-dialog
      v-model="show_roomPrice"
      :title="$route.query.sign?'修改房价':'设置'+changedMonth+'月'+changedDate+'号的房价'"
      :before-close="confirmOne"
      show-cancel-button
    >
      <div v-if="$route.query.sign">
        <van-field
          v-model="scaleName"
          label="渠道房型"
          readonly
        />
        <van-field
          v-model="otaSTime"
          label="日期"
          readonly
        />
        <van-field
          v-model="changedPrice"
          label="房价"
          type="number"
          placeholder="请输入房价"
        />
        <div class="otaTitle">携程价格按照低价设置，即最终结算给酒店的价格。</div>
      </div>
      <van-field
        v-else
        v-model="changedPrice"
        label="日租价"
        type="number"
        placeholder="请输入日租价格"
      />
    </van-dialog>
    <!-- 时间段 -->
    <van-dialog
      v-model="show_roomAmount"
      :title="$route.query.sign?'修改房量':'设置'+changedMonth1+'月'+changedDate1+'号'+'-'+changedMonth2+'月'+changedDate2+'号的房价'"
      :before-close="confirmMore"
      show-cancel-button
    >
      <div v-if="$route.query.sign">
        <van-field
          v-model="scaleName"
          label="渠道房型"
          readonly
        />
        <van-field
          v-model="otaSTime"
          label="日期范围"
          readonly
        />
        <div
          class="g_radio"
          style="padding:10px 25px;"
        >
          <div style="color:#646566;width:70px;text-align:left;">可售房量</div>
          <van-radio-group
            v-model="saleRadio"
            checked-color='#ff7d41'
            direction="horizontal"
          >
            <van-radio name="1">无限量</van-radio>
            <van-radio name="2">限量</van-radio>
          </van-radio-group>
        </div>
        <div
          v-if='saleRadio == 2'
          class="g_radio"
          style="padding:10px 25px;"
        >
          <div style="color:#646566;width:70px;text-align:left;">限量房量</div>
          <van-stepper
            v-model="LimitedStepper"
            :min="minStepper"
            :max="maxStepper"
          />
        </div>
      </div>
      <div v-else>
        <van-radio-group
          v-model="value"
          checked-color='#ff7d41'
          style="margin:20px 25%;"
        >
          <van-radio name="1">平时、周末不同价</van-radio>
          <van-radio
            name="2"
            class='g_mt10'
          >平时、周末统一价</van-radio>
        </van-radio-group>
        <div
          v-if="value == '1'"
          class="g_flex"
        >
          <div style="width:80%;">
            <div>平时价格</div>
            <div style="font-size:12px;">(周日-周四)</div>
          </div>
          <van-field
            v-model="ordinaryPrice"
            type="number"
            placeholder="请输入平时价格"
          />
        </div>
        <div
          v-if="value == '1'"
          class="g_flex"
        >
          <div style="width:80%;">
            <div>周末价格</div>
            <div style="font-size:12px;">(周五-周六)</div>
          </div>
          <van-field
            v-model="weekPrice"
            type="number"
            placeholder="请输入周末价格"
          />
        </div>
        <div
          v-if="value == '2'"
          class="g_flex"
        >
          <div style="width:80%;">
            <div>周一到周末价格</div>
            <div style="font-size:12px;">(周日-周四)</div>
          </div>
          <van-field
            v-model="DayPrice"
            type="number"
            placeholder="请输入日租价格"
          />
        </div>
      </div>
    </van-dialog>
    <van-popup
      v-model="show_otaMore"
      round
      @click-overlay='priceDataGet(virtualRoomScaleId || $route.query.virtualRoomScaleId)'
      style="width:90%;font-size:14px;padding:20px 0;"
    >
      <div>{{ otaSTime }} <span v-if="otaSTime && otaSTime.length > 10">共{{ diffDay + 1 }}晚</span></div>
      <div style="margin:10px 0 30px 0;">{{ scaleName }}</div>
      <div>
        <van-button
          class='vanButton'
          color="#ff7d41"
          @click="goNext('1')"
        >改房价</van-button>
        <van-button
          v-if="otaSTime && otaSTime.length == 10"
          class='vanButton'
          color="#ff7d41"
          @click="goNext('2')"
        >改房量</van-button>
      </div>
    </van-popup>
    <van-popup
      v-model="show_otaScale"
      position="bottom"
      style="width:100%;"
    >
      <van-picker
        title="标题"
        show-toolbar
        :columns="otaScaleList"
        @confirm="onConfirm"
        @cancel="show_otaScale = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { GetBusinessRoompriceBook, PostBusinessRoompriceEdit, PostBusinessRoompriceBatchAdd } from '@/api/api_business_hotel_and_scale';
import { GetBusinessRoomstatePriceBookVirtual } from '@/api/api_business_hotel_and_scale';
import { PostBusinessOtaPriceEdit, PostBusinessOtaPriceBatchEdit, PostBusinessRoomscaleStateEdit, PostBusinessRoomscaleStateBatchAdd } from '@/api/api_ota';
import dayjs from 'dayjs';
import { Toast } from 'vant';
export default {
    name: 'PriceDetail',
    data() {
        return {
            scaleName: null,
            changedPrice: null,
            changedDate: null,
            changedMonth: null,
            changedDate1: null,
            changedMonth1: null,
            changedDate2: null,
            changedMonth2: null,
            changedYear: null,
            otaSTime: null,
            diffDay: null,
            y: null,
            ordinaryPrice: null, // 平时价
            weekPrice: null, // 周末价
            DayPrice: null, // 统一价
            bookMonthNum: null, // 显示几个月
            roomScaleRooms: null, // 房型下所有房间
            startDay: null,
            endDay: null,
            virtualRoomScaleId: null,
            value: '1',
            saleRadio: '1',
            LimitedStepper: 0,
            minStepper: 0,
            maxStepper: 100,
            show_roomPrice: false,
            show_roomAmount: false,
            show_otaScale: false,
            show_otaMore: false,
            showArray: [],
            startIndex: [],
            endIndex: [],
            otaScaleList: [],
            weekList: ['日', '一', '二', '三', '四', '五', '六'],
            holiday: ['2022-09-10', '2022-09-11', '2022-09-12'],
            holidayName: {
                '2022-09-10': '中秋节',
                '2022-09-11': '中秋节',
                '2022-09-12': '中秋节'
            }
        };
    },
    created() {
        if (localStorage.getItem('ota_scaleList')) {
            this.otaScaleInfoList = JSON.parse(localStorage.getItem('ota_scaleList'));
            this.otaScaleList = this.otaScaleInfoList.map((item) => item.scaleName);
        }
        this.$store.commit('setHeader', '房价日历');
        this.bookMonthNum = this.$route.query.bookMonth;
        this.scaleName = this.$route.query.scaleName;
        this.pro_id = this.$route.query.pro_id;
        this.scale_id = this.$route.query.scale_id;
        this.priceDataGet(this.$route.query.virtualRoomScaleId);
    },
    methods: {
        async priceDataGet(virtualRoomScaleId) {
            try {
                this.startTime = null;
                this.endTime = null;
                this.endDay = null;
                this.startDay = null;
                let res = null;
                if (this.$route.query.sign) {
                    res = await GetBusinessRoomstatePriceBookVirtual(virtualRoomScaleId, { otaSource: this.$route.query.otaPlat });
                    let price = res.data.roomPrices;
                    let state = res.data.roomStates;
                    price.map((item) => {
                        state.map((_itm) => {
                            if (dayjs(item.day).unix() == dayjs(_itm.day).unix()) {
                                _itm.realAmount = item.realAmount;
                            }
                        });
                    });
                    this.dealRes = state;
                } else {
                    res = await GetBusinessRoompriceBook(this.pro_id, { roomScaleId: this.scale_id });
                    this.dealRes = res.data;
                }
                if (res.ret == 1) {
                    this.roomScaleRooms = this.dealRes;
                    let realAmountList = this.dealRes.map((item) => Number(item.realAmount).toFixed(2));
                    let roomBookList = this.dealRes.map((item) => item.roomBook);
                    let roomCountList = this.dealRes.map((item) => item.roomCount);
                    let maxSaleCountList = this.dealRes.map((item) => item.maxSaleCount);
                    let virtualRoomBookList = this.dealRes.map((item) => item.virtualRoomBook);
                    let dayList = this.dealRes.map((item) => item.day);
                    this.nowday = this.dealRes[0].day;
                    this.createArry(Number(this.bookMonthNum) + 1, realAmountList, roomBookList, roomCountList, maxSaleCountList, virtualRoomBookList, dayList); // 展示的月份
                }
            } catch (e) {
                console.log(e);
            }
        },
        pickScaleName() {
            if (this.otaScaleList.length == 0) {
                return;
            }
            this.show_otaScale = true;
        },
        onConfirm(val) {
            this.scaleName = val;
            this.virtualRoomScaleId = this.otaScaleInfoList.find((item) => item.scaleName == val).virtualRoomScaleId;
            this.show_otaScale = false;
            this.priceDataGet(this.virtualRoomScaleId);
        },
        createArry(num, realAmountList, roomBookList, roomCountList, maxSaleCountList, virtualRoomBookList, dayList) {
            let arr = [];
            const days = dayjs();
            let nowday;
            let j = 0;
            for (let i = 0; i < num; i++) {
                if (dayjs(this.nowday).format('YYYY-MM-DD') == days.format('YYYY-MM-DD')) {
                    nowday = i == 0 ? days : days.startOf('month').add(i, 'month');
                } else {
                    nowday = i == 0 ? days.subtract(1, 'day') : days.startOf('month').add(i, 'month');
                }
                const title = { y: nowday.year(), m: nowday.month() + 1 };
                const list = this.translateToArray(nowday).map((v) => {
                    if (v.days) {
                        const holiday = this.searchHoliday(v.nowTime);
                        let res = this.$route.query.sign
                            ? { date: v.days, price: realAmountList[j], holiday, roomBook: roomBookList[j], roomCount: roomCountList[j], maxSaleCount: maxSaleCountList[j], virtualRoomBook: virtualRoomBookList[j], time: dayList[j] }
                            : { date: v.days, price: realAmountList[j], holiday };
                        j++;
                        return res;
                    }
                    return v;
                });
                arr.push({ title, list });
            }
            this.showArray = arr;
        },
        searchHoliday(time) {
            return this.holiday.includes(time) ? this.holidayName[time] || '假日' : '';
        },
        translateToArray(days) {
            const start = days.date();
            const nowTime = days.format('YYYY-MM');
            const realstart = days.startOf('month').day();
            const end = days.endOf('month').date();
            const startweekday = days.day();
            const spans = end - start + 1;
            const myarry = Array.from({ length: spans }, (v, i) => {
                const nowTimeTemp = (i + start).toString().padStart(2, '0');
                return { days: i + start, nowTime: `${nowTime}-${nowTimeTemp}` };
            });
            const beforearry = Array(startweekday).fill(0);
            const last = 42 - (end + realstart);
            const afterarry = Array(last).fill(0);
            return [...beforearry, ...myarry, ...afterarry];
        },
        // 修改单天房价
        changePrice(ym, info, nindex, index) {
            this.LimitedStepper = info.maxSaleCount - info.virtualRoomBook < info.roomCount - info.roomBook ? info.maxSaleCount - info.virtualRoomBook : info.maxSaleCount - info.roomBook;
            this.minStepper = 0;
            this.maxStepper = info.roomCount - info.roomBook;
            this.info = info;
            if (info.price < 0) {
                this.show_roomPrice = false;
            } else {
                this.changedPrice = info.price;
                this.changedDate = info.date;
                this.changedMonth = ym.m;
                const data = this.showArray[nindex].list[index].date;
                if (typeof data == 'number' && !this.startDay) {
                    this.changedYear = ym.y;
                    this.startDay = data;
                    this.changedDate1 = this.startDay;
                    this.changedMonth1 = ym.m;
                    this.startIndex = [nindex, index];
                    this.showArray[nindex].list[index].date = '开始';
                    return;
                }
                if (data == '开始' && !this.endDay) {
                    this.changedYear = ym.y;
                    this.endDay = this.startDay;
                    this.showArray[nindex].list[index].date = '开始/结束';
                    this.changedDate = this.startDay;
                    this.otaSTime = this.changedYear + '年' + this.changedMonth + '月' + this.changedDate + '日';
                    if (this.$route.query.sign) {
                        this.show_otaMore = true;
                    } else {
                        this.show_roomPrice = true;
                    }
                    return;
                }
                if (data == '开始' && this.endDay) {
                    this.changedYear = ym.y;
                    this.showArray[nindex].list[index].date = this.startDay;
                    this.showArray[this.endIndex[0]].list[this.endIndex[1]].date = this.endDay;
                    this.endDay = '';
                    this.startDay = '';
                    return;
                }
                if (typeof data ==
 'number' && this.startDay && !this.endDay) {
                    if (nindex > this.startIndex[0] || (nindex == this.startIndex[0] && index > this.startIndex[1])) {
                        this.end_month = ym.y;
                        this.endDay = data;
                        this.changedDate2 = this.endDay;
                        this.changedMonth2 = ym.m;
                        this.endIndex = [nindex, index];
                        this.showArray[nindex].list[index].date = '结束';
                        if (this.$route.query.sign) {
                            this.otaSTime = this.changedYear + '-' + this.changedMonth1 + '-' + this.changedDate1 + '至' + this.changedYear + '-' + this.changedMonth2 + '-' + this.changedDate2;
                            this.diffDay = dayjs(this.changedYear + '-' + this.changedMonth2 + '-' + this.changedDate2).diff(dayjs(this.changedYear + '-' + this.changedMonth1 + '-' + this.changedDate1), 'day');
                            this.startTime = dayjs(this.changedYear + '-' + this.changedMonth1 + '-' + this.changedDate1).format('YYYY-MM-DD');
                            this.endTime = dayjs(this.changedYear + '-' + this.changedMonth2 + '-' + this.changedDate2).format('YYYY-MM-DD');
                            // let _arr = [];
                            // for (let i = 0; i <= this.diffDay; i++) {
                            //     _arr.push(dayjs(this.endTime).subtract(i, 'day').format('YYYY-MM-DD'));
                            // }
                            // let _LimitedStepperList = [];
                            // let _virtualRoomBook = [];
                            // let _roomBook = [];
                            // let _roomCount = [];
                            // this.dealRes.map((item) => {
                            //     _arr.map((_itm) => {
                            //         if (dayjs(_itm).unix() == dayjs(item.day).unix()) {
                            //             _LimitedStepperList.push(item.maxSaleCount - item.virtualRoomBook < item.roomCount - item.roomBook ? item.maxSaleCount - item.virtualRoomBook : item.maxSaleCount - item.roomBook);
                            //             _virtualRoomBook.push(item.virtualRoomBook);
                            //             _roomBook.push(item.roomBook);
                            //             _roomCount.push(item.roomCount);
                            //         }
                            //     });
                            // });
                            // console.log('_virtualRoomBook', _LimitedStepperList, Math.min(..._virtualRoomBook));
                            // console.log('_virtualRoomBook', _virtualRoomBook, Math.min(..._virtualRoomBook));
                            // console.log('_roomBook', _roomBook, Math.min(..._roomBook));
                            // console.log('_roomCount', _roomCount, Math.max(..._roomCount));
                            this.show_otaMore = true;
                        } else {
                            this.show_roomAmount = true;
                        }
                        return;
                    }
                }
                if (data == '开始/结束') {
                    this.showArray[nindex].list[index].date = this.startDay;
                    this.endDay = '';
                    this.startDay = '';
                    return;
                }
                if (data == '结束') {
                    this.changedYear = ym.y;
                    this.showArray[nindex].list[index].date = this.endDay;
                    this.showArray[this.startIndex[0]].list[this.startIndex[1]].date = this.startDay;
                    this.endDay = '';
                    this.startDay = '';
                    return;
                }
            }
        },
        confirmOne(action, done) {
            if (action == 'confirm') {
                done(false);
                if (!this.changedPrice) {
                    Toast('请正确输入日租价格');
                    this.changedPrice = '';
                    return;
                }
                let priceDate = this.changedYear + '-' + (this.changedMonth < 10 ? '0' + this.changedMonth : this.changedMonth) + '-' + (this.changedDate < 10 ? '0' + this.changedDate : this.changedDate);
                let editData = null;
                if (this.$route.query.sign) {
                    if (this.startTime) {
                        editData = {
                            week: 0,
                            startTime: this.startTime,
                            endTime: this.endTime,
                            virtualRoomScaleId: this.virtualRoomScaleId || this.$route.query.virtualRoomScaleId,
                            realAmount: this.changedPrice
                        };
                    } else {
                        editData = {
                            virtualRoomScaleId: this.virtualRoomScaleId || this.$route.query.virtualRoomScaleId,
                            realAmount: this.changedPrice,
                            priceDay: priceDate
                        };
                    }
                } else {
                    editData = {
                        roomScaleId: this.pro_id,
                        realAmount: this.changedPrice,
                        priceDate: priceDate
                    };
                }
                let _api = this.$route.query.sign ? (this.otaSTime && this.otaSTime.length > 10 ? PostBusinessOtaPriceBatchEdit : PostBusinessOtaPriceEdit) : PostBusinessRoompriceEdit;
                _api(editData).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    this.priceDataGet(this.virtualRoomScaleId || this.$route.query.virtualRoomScaleId);
                });
                this.show_roomPrice = false;
            } else if (action == 'cancel') {
                done(false);
                this.priceDataGet(this.virtualRoomScaleId || this.$route.query.virtualRoomScaleId);
                this.show_roomPrice = false;
            }
        },
        confirmMore(action, done) {
            if (action == 'confirm') {
                done(false);
                if (!this.$route.query.sign) {
                    if (this.value == '1' && !this.ordinaryPrice) {
                        Toast('请正确输入平时价格');
                        this.ordinaryPrice = null;
                        return;
                    }
                    if (this.value == '1' && !this.weekPrice) {
                        Toast('请正确输入周末价格');
                        this.weekPrice = null;
                        return;
                    }
                    if (this.value == '2' && !this.DayPrice) {
                        Toast('请正确输入日租价格');
                        this.DayPrice = null;
                        return;
                    }
                }
                let startTime = this.changedYear + '-' + (this.changedMonth1 < 10 ? '0' + this.changedMonth1 : this.changedMonth1) + '-' + (this.changedDate1 < 10 ? '0' + this.changedDate1 : this.changedDate1);
                let endTime = this.end_month + '-' + (this.changedMonth2 < 10 ? '0' + this.changedMonth2 : this.changedMonth2) + '-' + (this.changedDate2 < 10 ? '0' + this.changedDate2 : this.changedDate2);
                let addData = null;
                if (this.$route.query.sign) {
                    // if (this.otaSTime && this.otaSTime.length > 10) {
                    //     addData = {
                    //         roomScaleId: this.pro_id,
                    //         maxSaleRoom: this.LimitedStepper,
                    //         otaSource: this.$route.query.otaPlat,
                    //         startTime: startTime,
                    //         endTime: endTime
                    //     };
                    // } else {
                    addData = {
                        roomScaleId: this.pro_id,
                        maxSaleRoom: this.saleRadio == '1' ? this.maxStepper : this.LimitedStepper,
                        otaSource: this.$route.query.otaPlat,
                        stateDate: startTime
                    };
                    // }
                } else {
                    if (this.value == '1') {
                        addData = {
                            startTime: startTime,
                            endTime: endTime,
                            roomScaleId: this.pro_id,
                            week: this.value,
                            dayAmount: this.ordinaryPrice,
                            weekDayAmount: this.weekPrice
                        };
                    } else {
                        addData = {
                            startTime: startTime,
                            endTime: endTime,
                            roomScaleId: this.pro_id,
                            week: this.value,
                            realAmount: this.DayPrice
                        };
                    }
                }
                // let _api = this.$route.query.sign ? (this.otaSTime && this.otaSTime.length > 10 ? PostBusinessRoomscaleStateBatchAdd : PostBusinessRoomscaleStateEdit) : PostBusinessRoompriceBatchAdd;
                let _api = this.$route.query.sign ? PostBusinessRoomscaleStateEdit : PostBusinessRoompriceBatchAdd;
                _api(addData).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    this.ordinaryPrice = null;
                    this.weekPrice = null;
                    this.DayPrice = null;
                    this.priceDataGet(this.virtualRoomScaleId || this.$route.query.virtualRoomScaleId);
                });
                this.show_roomAmount = false;
            } else if (action == 'cancel') {
                done(false);
                this.priceDataGet(this.virtualRoomScaleId || this.$route.query.virtualRoomScaleId);
                this.show_roomAmount = false;
            }
        },
        goNext(type) {
            if (type == '1') {
                // 改房价
                this.show_roomPrice = true;
                this.show_otaMore = false;
            } else {
                // 改房量
                this.saleRadio = '1';
                this.show_roomAmount = true;
                this.show_otaMore = false;
            }
        }
        // overlay() {
        //     this.priceDataGet(this.virtualRoomScaleId || this.$route.query.virtualRoomScaleId);
        // }
    }
};
</script>
<style scoped>
.topbox {
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}
.scale {
    line-height: 32px;
    font-size: 18px;
    background-color: #fff;
}
.time {
    height: 25px;
    color: #000;
    background-color: #f6f6f6;
    line-height: 30px;
}
.datediv {
    background-color: #fff;
    color: rgb(20, 20, 20);
    font-size: 14px;
